import React from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
export const Background = () => (
	<Section
		title='Background theme color'
		context={<>by adding <b>.n-bg</b> class will change the background color to theme color.</>}>
		<HoverCopy code={code.Background}>
            <CodeReadOnly code={code.Background} />
        </HoverCopy>
        <div className="n-bg" style={{height: '100px'}}>
            it is not visible since it is the body's theme color but in some cases you will need it.
        </div> 
	</Section>
); 

export const BackgroundIntense = () => (
	<Section
		title='Background theme intensify color'
		context={<>by adding <b>.n-bg-intense</b> class will change the background color to theme color.</>}>
		<HoverCopy code={code.BackgroundIntense}>
            <CodeReadOnly code={code.BackgroundIntense} />
        </HoverCopy>
        <div className="n-bg-intense" style={{height: '100px'}}> 
        </div> 
	</Section>
);

export const TextColor = () => (
	<Section
		title='Text theme color'
		context={<>by adding <b>.n-text-color</b> class will change the text color although the body itself has the text color but in some cases in some element it is not applied.</>}>
		<HoverCopy code={code.TextColor}>
            <CodeReadOnly code={code.TextColor} />
        </HoverCopy>
        <a className="n-text-color" href='#' >
            text color 
        </a> 
	</Section>
);

export const TextDimColor = () => (
	<Section
		title='Text theme dim color'
		context={<>by adding <b>.n-text-dim-color</b> class it will change the color into a dimmer theme text color, and by applying <b>.n-text-hover</b> class beside it will hange the text color into the original theme text color.</>}>
		<HoverCopy code={code.TextDimColor}>
            <CodeReadOnly code={code.TextDimColor} />
        </HoverCopy>
        <a className="n-text-dim-color" href='#' >
            text dim color 
        </a><br/>
        <a className="n-text-dim-color n-text-hover" href='#' >
            text dim color with hover
        </a> 
	</Section>
);

export const BorderColor = () => (
	<Section
		title='Border color'
		context={<>by adding <b>.n-border-color</b> class will change the border color in to theme border color.</>}>
		<HoverCopy code={code.BorderColor}>
            <CodeReadOnly code={code.BorderColor} />
        </HoverCopy>
        <div className="n-border-color" style={{height: '100px', border: '1px solid red'}}> 
        </div> 
	</Section>
); 

export const Bordered = () => (
	<Section
		title='Bordered'
		context={<>by adding <b>.n-bordered</b> class will add theme border around the element with 1px width.</>}>
		<HoverCopy code={code.Bordered}>
            <CodeReadOnly code={code.Bordered} />
        </HoverCopy>
        <div className="n-bordered" style={{height: '100px'}}> 
        </div> 
	</Section>
); 